body,
html {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow: hidden;
    height: 100%;
}

.search_container {
    height: 100%;
    margin: 0;
    background-color: #FFF;

    .sidebar {
        height: 100%;
        z-index: 9;
    }

    .search_content_list_panel {
        display: flex;
        flex-direction: column;
        width: 100%;


        .middle_pane {
            flex: 1;

            .input_box_influencer {
                padding: 0.75rem;

                input {
                    background: #FFFFFF;
                    border: 0.063rem solid rgba(0, 0, 0, 0.2);
                    border-radius: 0.313rem;
                    box-sizing: border-box;
                    width: 50.625rem;
                    height: 3rem;
                    font-family: 'Noto Sans';
                    font-style: italic;
                    font-weight: 400;
                    font-size: 0.75rem;
                    line-height: 1rem;
                    color: rgba(0, 0, 0, 0.2);
                    padding-left: 1rem;
                }

                svg {
                    position: absolute;
                    top: 12.5%;
                    left: 62%;
                    cursor: pointer;
                }
            }

            .filter_bar {
                display: flex;
                padding-top: 1rem;

                .filter_btn {
                    margin-left: 1rem;
                    margin-right: 1rem;
                    margin-bottom: 2rem;
                    background: #FFFFFF;
                    border: 0.063rem solid rgba(0, 0, 0, 0.2);
                    border-radius: 0.313rem;
                    font-family: 'Noto Sans';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 0.875rem;
                    line-height: 1.188rem;
                    color: rgba(0, 0, 0, 0.7);
                    height: 2.5rem;
                    text-transform: none;
                }

                .modal_section {
                    position: absolute;
                    z-index: 4;
                    top: 60%;
                    left: 27%;
                    transform: translate(-50%, -50%);
                    width: 24rem;
                    border-radius: 0.5rem;
                    background: #FFFFFF;
                    border: 0.063rem solid rgba(0, 0, 0, 0.2);
                    box-shadow: 0.125rem 0.25rem 0.25rem rgba(0, 0, 0, 0.25);
                    border-radius: 0.313rem;
                    padding-left: 1rem;
                    padding-right: 3rem;
                    padding-top: 1rem;
                    padding-bottom: 1rem;
                    height: 28.75rem;

                    .modal_option {
                        max-height: 21.375rem;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        cursor: pointer;
                        // padding: 1rem;

                        .modal_title {
                            font-family: 'Noto Sans';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 1rem;
                            line-height: 1.375rem;
                            color: #000000;
                        }

                        .label_slider {
                            font-family: 'Noto Sans';
                            font-style: normal;
                            font-weight: 700;
                            font-size: 0.875rem;
                            line-height: 1.188rem;
                            color: RGB(0, 0, 0, 0.5);
                            padding-top: 1rem;
                        }

                        .css-b62m3t-container {
                            min-width: 15.313rem;
                        }

                        .slider_dropdown {
                            background: #FFFFFF;
                            border: 0.063rem solid rgba(0, 0, 0, 0.5);
                            border-radius: 0.313rem;
                            box-sizing: border-box;
                            height: 2.25rem;
                            width: 16.25rem;
                            margin-top: 1rem;
                            font-family: 'Inter';
                            font-style: normal;
                            font-weight: 400;
                            font-size: 1rem;
                            line-height: 1.188rem;
                            color: rgba(0, 0, 0, 0.4);
                            padding-left: 0.75rem;

                            option {
                                font-family: 'Inter';
                                font-style: normal;
                                font-weight: 400;
                                font-size: 1rem;
                                line-height: 1.188rem;
                                color: rgba(0, 0, 0, 0.8);
                            }
                        }

                        .css-1aznpnh-MuiSlider-root {
                            color: #7662DC;
                            border-radius: 0.313rem;
                            margin: 0;
                            margin-top: 1rem;
                        }

                        .css-yafthl-MuiSlider-markLabel {
                            font-size: 0.625rem;
                        }

                        .css-1eoe787-MuiSlider-markLabel {
                            font-size: 0.625rem;
                        }

                        .css-14pt78w-MuiSlider-rail {
                            background: #D9D9D9;
                            border-radius: 0.313rem;
                        }

                        .css-eg0mwd-MuiSlider-thumb {
                            background: #FFFFFF;
                            border: 0.063rem solid RGB(0, 0, 0, 0.2);
                            box-shadow: 0 0.063rem 0.125rem RGB(0 0 0 / 25%);
                        }

                        .followers_count {
                            font-family: 'Noto Sans';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 0.75rem;
                            line-height: 1rem;
                            color: RGB(0, 0, 0, 0.5);
                            margin-top: 0.625rem;
                        }

                        .followers_count_1 {
                            font-family: 'Noto Sans';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 0.75rem;
                            line-height: 1rem;
                            color: RGB(0, 0, 0, 0.5);
                            margin-top: 2rem;
                        }

                        input {
                            margin: 0.75rem;
                        }

                        .css-1e6y48t-MuiButtonBase-root-MuiButton-root {
                            background: #7662DC;
                            border-radius: 0.188rem;
                            width: 7.75rem;
                            height: 2.25rem;
                            font-family: 'Noto Sans';
                            font-style: normal;
                            font-weight: 700;
                            font-size: 0.75rem;
                            line-height: 1rem;
                            text-align: center;
                            color: #FFFFFF;
                            margin-top: 2rem;
                        }
                    }
                }

                .download_icon {
                    margin-left: 0.75rem;
                    padding-top: 0.313rem;
                }
            }

            .table_paper {
                overflow-y: scroll;
                height: 68vh;
                color: rgba(0, 0, 0, 0.87);
                -webkit-transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
                transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
                border-radius: 0.25rem;
                box-shadow: none;
                width: 100%;
            }

            .table_content {
                padding-left: 0.5rem;
                padding-right: 0.5rem;

                .table_container {
                    background-color: #FFF;

                    .table_row {
                        background-color: #FFF !important;
                    }

                    .table_head_value {
                        background: #D9D9D9;
                        font-family: 'Noto Sans';
                        font-style: normal;
                        font-weight: 700;
                        font-size: 0.875rem;
                        line-height: 1rem;
                        color: rgba(0, 0, 0, 0.5);
                    }

                    .table_body_value_1 {
                        background-color: #FFF !important;
                        font-family: 'Noto Sans';
                        font-style: normal;
                        font-weight: 500;
                        font-size: 1rem;
                        line-height: 1.188rem;
                        color: #000000;
                        cursor: pointer;
                    }

                    .table_body_value {
                        background-color: #FFF !important;
                        font-family: 'Noto Sans';
                        font-style: normal;
                        font-weight: 500;
                        font-size: 0.875rem;
                        line-height: 1.188rem;
                        color: #000000;

                    }

                    .btn_display {
                        display: flex;

                        img {
                            margin-left: 0.625rem;
                            margin-right: 0.625rem;
                        }
                    }

                    .cost_modal {
                        position: absolute;
                        background: #FFFFFF;
                        border: 0.063rem solid rgba(0, 0, 0, 0.2);
                        box-shadow: 0.125rem 0.25rem 0.25rem rgba(0, 0, 0, 0.25);
                        border-radius: 0.313rem;
                        width: 22.813rem;
                        height: 22.938rem;
                        top: 40%;
                        left: 68%;
                        padding-bottom: 1rem;

                        .modal_title {
                            font-family: 'Noto Sans';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 1rem;
                            line-height: 1.375rem;
                            padding: 1rem 1.5rem;
                            color: #000000;
                        }

                        .modal_desc,
                        .cost_box {
                            font-family: 'Noto Sans';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 0.875rem;
                            line-height: 1.188rem;
                            padding: 0 1.5rem;
                            color: rgba(0, 0, 0, 0.7);
                        }

                        .cost_box {
                            display: flex;
                            padding: 0 1.5rem;

                            .cost_value {
                                padding-left: 6rem;
                            }
                        }

                        .modal_footer {
                            font-family: 'Noto Sans';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 0.875rem;
                            line-height: 1.188rem;
                            padding: 0 1.5rem;
                            color: rgba(0, 0, 0, 0.7);
                            padding: 3rem 1.5rem 0;

                        }

                        .close_btn {
                            background: #D7D7D7;
                            border-radius: 0.188rem;
                            font-family: 'Noto Sans';
                            font-style: normal;
                            font-weight: 600;
                            font-size: 0.75rem;
                            line-height: 1rem;
                            text-align: center;
                            color: #595959;
                            width: 7.75rem;
                            height: 2.25rem;
                            margin: 1rem 1.5rem;
                        }
                    }

                    .addList_section {
                        max-height: 21.875rem;
                        position: absolute;
                        z-index: 4;
                        padding: 0.75rem;
                        background: #FFFFFF;
                        border: 0.063rem solid RGB(0, 0, 0, 0.2);
                        box-shadow: 0.125rem 0.25rem 0.25rem RGB(0 0 0 / 25%);
                        border-radius: 0.313rem;
                        top: 50%;
                        left: 70%;

                        .addList_option {
                            max-height: 21.375rem;
                            display: flex;
                            flex-direction: column;
                            justify-content: space-between;
                            padding: 0.625rem;
                            border-radius: 0.5rem;
                            cursor: pointer;

                            .section_dropdown_header {
                                font-family: 'Noto Sans';
                                font-style: normal;
                                font-weight: 500;
                                font-size: 1rem;
                                line-height: 1.375rem;
                                color: #000000;
                            }

                            .section_list_title {
                                font-family: 'Noto Sans';
                                font-style: normal;
                                font-weight: 500;
                                font-size: 0.875rem;
                                line-height: 1.188rem;
                                color: rgba(0, 0, 0, 0.7);
                                margin-top: 0.5rem;
                            }

                            .list_options {
                                font-family: 'Inter';
                                font-style: normal;
                                font-weight: 400;
                                font-size: 1rem;
                                line-height: 1.188rem;
                                color: rgba(0, 0, 0, 0.8);
                                padding-top: 0.5rem;
                                padding-bottom: 0.5rem;
                            }
                        }
                    }

                    .compare_section {
                        position: absolute;
                        z-index: 4;
                        transform: translate(-50%, -50%);
                        background-color: white;
                        padding: 1rem 1.5rem;
                        background: #FFFFFF;
                        border: 0.063rem solid RGB(0, 0, 0, 0.2);
                        box-shadow: 0.125rem 0.25rem 0.25rem RGB(0 0 0 / 25%);
                        border-radius: 0.313rem;
                        box-sizing: border-box;
                        width: 25rem;
                        min-height: 22.688rem;
                        top: 72%;
                        left: 85%;

                        .close_btn {
                            display: flex;
                            justify-content: flex-end;
                        }

                        .compare_headers {
                            font-family: 'Noto Sans';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 1rem;
                            line-height: 1.375rem;
                            color: #000000;
                        }

                        .compare_title {
                            font-family: 'Noto Sans';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 0.875rem;
                            line-height: 1.188rem;
                            color: rgba(0, 0, 0, 0.7);
                            margin-top: 1rem;
                        }

                        .compare_input {
                            background: #FFFFFF;
                            border: 0.063rem solid rgba(0, 0, 0, 0.5);
                            border-radius: 0.313rem;
                            width: 19rem;
                            height: 2.25rem;
                            box-sizing: border-box;
                            margin-top: 0.5rem;
                        }

                        .suggestions {
                            position: absolute;
                            background: #FFFFFF;
                            border: 0.063rem solid rgba(0, 0, 0, 0.5);
                            border-radius: 0.313rem;
                            z-index: 9999;
                            width: 19rem;
                            max-height: 9.375rem;
                            overflow-y: scroll;
                            top: 40%;
                            left: 6%;

                            .active_inf {
                                padding: 0.75rem;
                            }

                            .non_active {
                                padding: 0.75rem;
                            }
                        }

                        .influencers_box {

                            .added_influencer {
                                background: #D9D9D9;
                                border-radius: 0.625rem;
                                width: 17rem;
                                display: flex;
                                align-content: center;
                                align-items: center;
                                padding: 0.75rem;
                                justify-content: space-between;
                                margin-top: 0.5rem;
                                font-family: 'Noto Sans';
                                font-style: normal;
                                font-weight: 600;
                                font-size: 0.75rem;
                                line-height: 1rem;
                                color: rgba(0, 0, 0, 0.7);
                            }

                            .clear_all {
                                font-family: 'Noto Sans';
                                font-style: normal;
                                font-weight: 700;
                                font-size: 0.875rem;
                                line-height: 1rem;
                                color: #007DA8;
                                margin-top: 0.5rem;
                            }

                            .btn_pane {
                                display: flex;
                                justify-content: space-between;
                                margin-top: 2rem;

                                .compare_btn {
                                    background: #7662DC;
                                    border-radius: 0.188rem;
                                    font-family: 'Noto Sans';
                                    font-style: normal;
                                    font-weight: 700;
                                    font-size: 0.75rem;
                                    line-height: 1rem;
                                    text-align: center;
                                    color: #FFFFFF;
                                    height: 2rem;
                                }

                                .clear_btn {
                                    background: #D7D7D7;
                                    border-radius: 0.188rem;
                                    font-family: 'Noto Sans';
                                    font-style: normal;
                                    font-weight: 700;
                                    font-size: 0.75rem;
                                    line-height: 1rem;
                                    text-align: center;
                                    color: #595959;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.css-fikjyc-MuiTableCell-root-MuiTablePagination-root {
    overflow: hidden;
    border-bottom: none !important;
}

.pointer {
    cursor: pointer;
}

.css-78c6dr-MuiToolbar-root-MuiTablePagination-toolbar {
    width: max-content;
}

.suggestions_influencer {
    background: #FFFFFF;
    border: 0.063rem solid RGB(0, 0, 0, 0.2);
    box-shadow: 0 0.25rem 0.25rem RGB(0 0 0 / 25%);
    border-radius: 0.313rem;
    width: 50.5rem;
    max-height: 15rem;
    position: absolute;
    z-index: 9999;
    top: 17%;
    left: 12.2%;

    overflow-y: scroll;

    .active_influencer {
        margin-left: 1rem;
        padding: 1rem;
        font-family: 'Noto Sans';
        font-style: normal;
        font-weight: 500;
        font-size: 1rem;
        line-height: 1.188rem;
        color: rgba(0, 0, 0, 0.7);
        cursor: pointer;
    }

    div {
        margin-left: 1rem;
        padding-left: 1rem;
        font-family: 'Noto Sans';
        font-style: normal;
        font-weight: 500;
        font-size: 1rem;
        line-height: 1.188rem;
        color: rgba(0, 0, 0, 0.7);
        cursor: pointer;
        padding-bottom: 1rem;
    }
}

